/* Home Page Styling */
.profile{
  border-radius: 50%;
  width: 25%;
  height: auto;
}

.home-body h1 {
  font-size: 6vh;
}

.home-body h4 {
  font-size: 3vh;
}

.home-body p {
  font-size: 2vh;
}

.home-body{
  height:100%;
}

/* Images Page Styling */
.images-body{
  text-align: center;
  overflow-y: auto;
}

.image{
  width: 65%;
  height: auto;
}

.image:hover {
  transform: scale(1.1);
}

.images-body h1 {
  font-size: 8vh;
}

.images-body h4 {
  font-size: 3vh;
}

.bg-modal{
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.7);
  position: absolute;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content{
  width: 40%;
  height: 50%;
  background: white;
}

/* Videos Styling */
.videos-body h4{
  overflow-y: auto;
  font-size: 3vh;
}

iframe {
    width: 100%;
    height: 80%;
}

/* Projects Styling */
.projects-body{
  overflow-y: auto;
}

.projects-body h4{
  overflow-y: auto;
  font-size: 3vh;
}

/* App Styling */
.NavBar{
  padding: 0px !important;
}

.active-nav-tab{
  background-color: black !important;
}

#btnScrollToTop{
  position: fixed;
  right: 35px;
  bottom: 10px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: #cf2727;
  color: white;
  outline: none;
  cursor: pointer;
  border: none;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
}

#btnScrollToTop:active{
  background: #942828;
}
